.selectCountry{
    display: flex;
    margin-top: 20px;
    justify-content: center;
}
.selectCountry select{
    width: 450px;
    font-size: 16px;
}
@media screen and (max-width: 800px) {
    .selectCountry {
      width: 99%; /* The width is 100%, when the viewport is 800px or smaller */
    }
    .selectCountry select{
        width: 95%;
    }
}