.red{
    background-color: red;
}
.mycard{
    width:200px;
    border: solid 1px;
    background-color: white;
    margin: 0 10px;
}
.mycard h2{
    padding: 10px 20px;
    border-bottom: solid 1px rgba(48, 42, 42, 0.233);
    margin-bottom: 2px;
}
.mycard p{
    font-size: 16px;
    padding: 20px 20px;
    margin: 0;
    color: rgb(39, 13, 13);
} 
@media screen and (max-width: 800px) {
    .mycard {
      width: 90%;
      margin: 15px auto;
    }
  }
